import React from 'react';

const emailStyle = {
  position: 'fixed',
  right: '30px',
  bottom: '27px',
  fontSize: '10px',
  cursor: 'default'
};

const email = () => <div style={emailStyle}>hello@lektorij.ru</div>;

export default email;



